import React, { useRef ,useState, useEffect } from 'react';
import { motion } from "framer-motion";
import ReactScrollWheelHandler from "react-scroll-wheel-handler"; //stricMode dom property warring
import List from './list';
import {listData} from './listdata';

const Work = ({headerSub, isWork, workState}) => {
  const workList = useRef(null);
  const currentRef = useRef(null);
  const [activeNum, setActiveNum] = useState(0);
  const doPrev = Math.max(0, activeNum - 1);
  const doNext = Math.min(listData.length - 1, activeNum + 1);

  const prev = () => {
    setActiveNum(doPrev);
    workList.current.style.transform = 'translateX(-' + (currentRef.current.offsetWidth) * doPrev +'px)';
    //console.log('prev', activeNum);
  };

  const next = () => {
    setActiveNum(doNext);
    workList.current.style.transform = 'translateX(-' + (currentRef.current.offsetWidth) * doNext +'px)';
    //console.log('next', activeNum);
  };

  const pageVariants = {
    initial: {
      opacity: 0,
      y: '200px',
      scale: 0.8
    },
    in: {
      opacity: 1,
      y: 0,
      scale: 1
    },
    out: {
      opacity: 0,
      y: '200px',
      scale: 0.8
    },
  }
  const pageTransition = {
    type: "tween",
    ease: "easeInOut",
    delay: 0.3,
    type: "spring", stiffness: 100,
    transition: {
      delayChildren: 0.2,
      staggerDirection: -1
    }
  };

  useEffect(() => {
    headerSub ();
    // setTimeout(()=>{
    //   workState(true);
    // },300);
  }, []);


  return (
    <motion.div
      style={{position: "absolute",width:"100%", zIndex:"2"}}
      initial="initial"
      animate="in"
      exit="out"
      variants={pageVariants}
      transition={pageTransition}
    >
    <div className={"workCircle" + (isWork ? ' isWork' : '')}>
      <ReactScrollWheelHandler
        upHandler={prev}
        downHandler={next}
        customStyle={{
          width: "100%",
          height: "calc(100vh - 135px)",
        }}
      >
        <div className="list">
          <h3>Work<i></i></h3>
          <ul ref={workList}>
            {listData.map((site, idx) => {
              const active = idx === activeNum;
              //console.log(active, idx === activeEl);
              return (
                <List 
                  key={site.id}
                  id={site.id}
                  site={site}
                  active={active} 
                  currentRef={currentRef}  
                />
              )
            })}
          </ul>
          <span className="circle"></span>
        </div>
      </ReactScrollWheelHandler>
    </div>
    </motion.div>
  )
};
export default Work;